body {
  overflow-x: hidden;
}

/* <main> 태그 적용 사항 */
.App-main > div[class^='main__'] {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 740px;
  width: 100%;
  padding-bottom: 10px;
}

.App-main > div.main__banner {
  height: 400px;
  padding: 0;
}

/* 홀수번째 엘리먼트 */
.App-main > div:nth-child(odd) {
  background-color: white;
}
/* 짝수번째 엘리먼트 */
.App-main > div:nth-child(even) {
  background-color: #f5f5f5;
}

/* 모든 컴포넌트에 공통적으로 적용 사항 */
.App-main div[class$='text-box'] {
  /* height: 100%; */
  display: flex;
  flex-direction: column;
}
.App-main div[class$='text-box'] img[alt='main__logo'] {
  height: 24.7px;
  margin-bottom: 5px;
}
.App-main h3[class$='title'] {
  font-size: 46px;
  font-weight: bold;
  line-height: 1.3;
  word-break: keep-all;
  margin-bottom: 12px;
  color: #3b3b3b;
}
.App-main div[class$='text-box'] p {
  font-size: 20px;
  margin-top: 0;
}
.App-main div[class$='image-box'] {
  /* height: 100%; */
  position: relative;
}

.App-footer {
  height: 256.58px;
  width: 100%;
  background-color: #0a2e67;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  padding: 50px 10px 30px;
}

.App-footer img {
  margin-top: 15px;
  width: 119px;
}

.App-footer .footer__welcome {
  margin: 10px 0 30px;
  font-weight: 600;
  font-family: 'Poppins', sans-serif;
}

.App-footer .footer__info {
  font-weight: 400;
  text-align: center;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  font: 14px/1.5 'Noto Sans KR', sans-serif;
  /* font-family: 'Times New Roman', Times, serif; */
  color: #3b3b3b;
}

html,
body,
div,
span,
h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
img {
  margin: 0;
  border: 0;
  padding: 0;
  font-size: 100%;
  line-height: 150%;
  background: transparent;
}

a,
a:hover {
  color: white;
}

